*{
  margin: 0;
  padding:0;
}
body {
  
  font-family: 'Fraunces', serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;  
  background-color: hsl(30, 38%, 92%);    

}

code {
  font-family: 'Fraunces', serif;
}

.container{
  display:flex;
  flex-direction: row;
  width: 600px;
  height: 450px;
  box-shadow: 0 6px 10px rgba(0,0,0,0.2);
  border-radius: 10px;

}
.thumbnail{  
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  width: 50%;
  
}
.product-details{
  display: flex;
  flex-direction: column;  
  background-color: white;
  justify-content: space-between;
  padding:25px;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
}
.product-type{
  color: hsl(228, 12%, 48%);
  letter-spacing: 3px;
  text-transform: capitalize;
  font-weight: 500;
  font-size: 14px;
}
.product-name{
  font-size:35px;
  font-weight: 700;
  color: hsl(212, 21%, 14%);
}
.product-desc{
  color: hsl(228, 12%, 48%);   
  font-size: 15px;
  line-height: 20px;
  letter-spacing: 1px;

}
.product-price{
  display: flex;  
  flex-direction: row;
  align-items: center;

}
.product-selling{
  color:  hsl(158, 36%, 37%);
  font-weight: bold;
  font-size: 30px;  
  margin-right: 5px;
}
.product-mrp{
  text-decoration: line-through;
  font-size:12px;
  color: hsl(228, 12%, 48%);
  margin-left:5px;
}

.addToCart{
  background-color:  hsl(158, 36%, 37%);
  border-radius: 10px;
  border: none;
  height: 45px;
  color: white; 
}
.addText{
  font-size:14px;   
  font-family: 'Fraunces', serif;
  letter-spacing: 1px;  
  margin-left: 8px;
}



@media only screen and (max-width: 375px) {

  body{
    max-width: 375px;
    margin:0;
    padding:0;
    align-items: center;
  }
  .container{
    display: flex;
    flex-direction: column;    
    width: 94%;
    height: 600px;
    margin: auto;
  }
  .thumbnail{
    width: 100%;
    border-top-right-radius: 10px;
    border-bottom-left-radius: 0;    
    object-fit: cover;  
  }
  .product-details{
    border-bottom-left-radius: 10px;
    border-bottom-right-radius: 10px;
    justify-content: space-between;
    height: 100%;
  }
  .product-name{
    font-size:35px;
  }

}
